.me .content{
    margin-top: 60px;
    padding: 20px 5% 55px 5%;
    background-color: #fff;
    border-radius: 2px;
    -webkit-box-shadow: 0 1px 2px 0 rgba(0,0,0,.05);
            box-shadow: 0 1px 2px 0 rgba(0,0,0,.05);
    width: 70%;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
}

.me .content .cell {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: end;
        align-items: flex-end;    
    margin-top: 35px;
    -ms-flex: 1 1;
        flex: 1 1;
    -ms-flex-pack: justify;
        justify-content: space-between;
}

.me .content .cell .item{
    width: 100%;
}

.me .content .button{
    color: #ffffff;    
    background: #0f88eb;
    margin-top: 10px        
}

.me .content .cell .divb{
    display: -ms-flexbox;
    display: flex;
    margin-left: 5%;    
}

.me .content .b{
    margin-top: 10px
}

@media (max-width: 1024px) {
    .me .content{
        width: 100%;
    }
    .me .content{
        margin-top: 135px;
    }
    .me .content .cell {
        -ms-flex-wrap: wrap;
            flex-wrap: wrap;
    }
    .me .content .cell .divb{
        margin: 0;
        -ms-flex: 1 1;
            flex: 1 1;
        margin-top: 20px;
        -ms-flex-pack: center;
            justify-content: center;
    }

    .me .content .cell .button{
        width: 100%
    }
}


